.card-md-full-mobile{
    @media screen and (max-width: $sm-break-down){
        
        position:relative;
        margin-bottom: 20px;
        height:190px;

        a.card {
            height:100%;
            @include card-image-full();
            @include tintbefore();

            > article {
                position:relative;

                > figure{
                    margin-bottom:10px;

                }

                > .content{
                    position:relative;
                    padding: 16px 10px 10px 10px;
                    text-align:center;
                    z-index:10;
                    color:#fff;

                    > .category{
                        display:block;
                        color:white;
                        font-size:rem(15);
                        font-weight:400;
                        margin-bottom:70px;
                    }
                    time {
                        display:none;
                    }
                    > h2{
                        font-size: rem(16);
                        font-weight:400;
                        margin-top:2px;
                        margin-bottom:13px;
                        line-height: 1.44;
                        text-transform:uppercase;
                    }

                    > p.excerpt {
                        display:none;
                    }


                    .author {
                        display: block;

                        >p {
                            font-size:rem(12);
                            color:white;
                            font-family: $font-family-serif;
                            &:before {
                                content:'By ';
                            }
                        }
                        >img {
                            display:none;
                        }
                    }



                }
            }


            &.article.without__image {
                background:color(darkblue);
            }   
        }
    }
}
